:root {
  --colorHighlight: #000;
  --colorSecondary: #e6e6e6;
  --colorText: #595959;
  --colorTextSecondary: #999;
  /*--fontText: "Roboto", "Helvetica Neue", Helvetica, sans-serif;*/
  --fontText: -apple-system, "BlinkMacSystemFont", "Roboto", "Helvetica Neue", Helvetica, sans-serif;
  --fontHeading: -apple-system, "BlinkMacSystemFont", "Roboto", "Helvetica Neue", Helvetica, sans-serif;
  --light: 300;
  --regular: 400;
  --medium: 500;
  --bold: 700;

  --containerFull: {
    position: absolute;
    width: 100%;
    left: 0;
    right: 0;
  };

  --containerReading: {
    @media (min-width: 35rem) {
      width: 35rem;
      margin-left: auto;
      margin-right: auto;
    }
  };

  --containerExtrapolate: {
    margin-top: 15px;
    margin-bottom: 15px;
    max-width: 100%;
    border-radius: 3px;

    @media (min-width: 45rem) {
      max-width: 120%;
      margin-left: -10%;
    }

    @media (min-width: 63.125rem) {
      max-width: 180%;
      margin-left: -40%;
    }
  };

  --ellipsis: {
    overflow: hidden;
    text-overflow: ellipsis;
    white-space: nowrap;
  };

  --unstyledList: {
    list-style-image: none;
    list-style-type: none;
    margin: 0;
    padding: 0;
  };

  --optimizeFont: {
    font-smooth: always;
    -webkit-font-smoothing: antialiased;
    text-rendering: optimizeLegibility;
    -moz-osx-font-smoothing: grayscale;
  };
}
