.articles {
  @apply --unstyledList;
}

.article {
  margin-bottom: 0.75em;
  line-height: 1.5;

  &__pt-tag {
   font-size: 12px;
   color: var(--colorTextSecondary);
   display: inline-block;
   padding-top: 7px;
   padding-left: 5px;
  }

  @media (min-width: 52.5rem) {
    position: relative;
  }

  @media (min-width: 30rem) {
    text-align: left;
  }

  &-header {
    margin-bottom: 1em;

    @media (min-width: 26rem) {
      text-align: center;
    }
  }

  &__tags {
    font-size: 12px;
    margin-top: 10px;
  }

  &__title {
    margin-top: 0.2em;

    @media (min-width: 45rem) {
      width: 45rem;
      margin-left: -5rem;
    }

    &--list {
      text-align: center;
      margin-right: 0.75em;
      width: auto;
      font-size: 1.25em;
      line-height: 1.45;
      color: var(--colorText);
      /*border-bottom: 2px solid var(--colorSecondary);*/
      /*padding-bottom: 3px;*/

      &:-webkit-any-link {
        padding: 0;
      }

      &:hover {
        border-color: var(--colorHighlight);
      }
    }
  }

  &__date {
    vertical-align: middle;
    display: block;
    padding-top: 8px;
    font-size: .7em;
    text-transform: uppercase;
    color: var(--colorTextSecondary);
    letter-spacing: 2.5px;
    white-space: nowrap;
    line-height: 1.5;

    &--archive {
      @media (min-width: 52.5rem) {
        display: inline-block;
        padding-top: 5px;
        left: -11.5em;
        position: absolute;
        text-align: right;
        width: 10em;
      }
    }
  }

  &__normal-img {
    margin-top: 15px;
    margin-bottom: 15px;
    max-width: 100%;
    border-radius: 3px;
  }

  &__summary {
    font-style: italic;
    color: var(--colorTextSecondary);
    margin-top: -0.8rem;
    margin-bottom: 1.5em;

    @media (min-width: 26rem) {
      text-align: center;
    }

    @media (min-width: 36.25rem) {
      width: 80%;
      margin-left: auto;
      margin-right: auto;
    }
  }

  &__footnotes {
    font-size: 0.85em;
    color: var(--colorText);
  }

  & img:not(.article__normal-img) {
    @apply --containerExtrapolate;
  }
}

.nav-article {
  &__item {
    font-size: 0.8em;
    color: var(--colorTextSecondary);

    &--next {
      float: right;
    }
  }
}

#disqus_thread {
  margin-top: 2rem;
}

.back-icon {
  margin-left: 2px;
}

.katex-display {
  margin: 2.5em 0 -1.5em 0;
}
