.header {
  margin-bottom: 1.5em;
  overflow: auto;
  margin: auto;
  font-family: var(--fontHeading), sans-serif;

  @media (min-width: 890px) {
    width: 70%;
  }

  @media (min-width: 1400px) {
    width: 50rem;
  }

  &__personal {
    text-align: center;

    &__container {
      margin-top: 20px;

      @media (max-width: 35rem) {
        display: none;
      }
    }

    &__name {
      color: var(--colorHighlight);
      font-size: 20px;
    }

    &__subname {
      margin-top: -2px;
      display: block;
      font-size: 12px;
      opacity: 0.80;
    }
  }
}

.avatar {
  text-align: center;
  display: inline-block;

  &__pic {
    max-width: 100%;
    border-radius: 50%;
    width: 120px;
  }
}

.nav {
  text-align: center;
  margin-bottom: 2em;

  &__item {
    border-bottom: 0;
    color: var(--colorText);
    padding: 8px 10px;
    font-size: 1em;
    margin-left: 6px;
    margin-right: 6px;
    display: inline-block;

    &:hover, &:active {
      background: transparent;
      color: var(--colorHighlight);
      outline: 0;
    }

    &--active {
      color: var(--colorHighlight);
      font-weight: var(--bold);
    }

    &--external {
      color: inherit;
    }
  }

  &--internal {
    @apply --unstyledList;
    display: inline-block;

    & > li {
      display: inline-block;
    }
  }
}
