.content {
  margin-top: 1em;
  clear: both;
  @apply --containerReading;

  @media (min-width: 35rem) {
    margin-top: 3.5em;
  }
}

.title-section, h1, h2, h3, h4, h5, h6 {
  line-height: 1;
}

h1, h2, h3, h4, h5, h6 {
  margin-top: 1.65em;
}

.title-section {
  text-align: center;
  margin-top: 1.2em;
  margin-bottom: 0.8em;
  color: var(--colorTextSecondary);
  font-size: 2em;
  font-weight: var(--light);

  &:not(:first-child) {
    margin-top: 1.6em;
  }

  @media (min-width: 30rem) {
    text-align: left;
  }

}

.page__title {
  font-size: 2.4em;
  margin-bottom: .5em;
}
