html {
  /*border-top: 3px solid var(--colorHighlight);*/
}

body {
  background: #fafafa;
  color: var(--colorText);
  font-family: var(--fontText), sans-serif;
  margin: 1em 1.5em;
  @apply --optimizeFont;
}

h1, h2, h3, h4, h5, h6 {
  font-family: var(--fontHeading), sans-serif;
  margin: 0.5em 0;
  font-weight: var(--bold);
  font-style: normal;
}

h1 {
  color: var(--colorText);
  font-size: 2.2em;

  @media (min-width: 30rem) {
    font-size: 2.75em;
  }
}

h2 {
  font-size: 1.8em;
  color: var(--colorHighlight);
}

h3 {
  font-size: 1.6em;
  color: var(--colorTextSecondary);
}

h4 {
  font-size: 1em;
  font-weight: var(--bold);
  text-transform: uppercase;
}

h5 {
  font-size: 1em;
}

h6 {
  font-size: 0.8em;
}

hr {
  border-bottom: 2px solid var(--colorSecondary);
  margin: 1.5em 0;
  width: 90px;
}

p {
  margin-bottom: 1.25em;

  & a {
    border-bottom: 1px solid var(--colorHighlight);
    padding-bottom: 2px;
  }

  & a:-webkit-any-link {
    padding: 0;
  }
}

a {
  color: var(--colorHighlight);
  cursor: pointer;

  &:hover {
    /*border-style: solid;*/
    border-color: transparent;
    color: var(--colorHighlight);
  }

  &:focus {
    background: var(--colorHighlight);
    color: #fff;
    outline: 0;
  }
}

.article {
  font-size: 1rem;

  & li {
    margin-bottom: 0.85em;
    font-size: 0.9em;
    line-height: 1.5em;
  }

  & ul {
    padding-bottom: 0.25em;
  }

  & ul li {
    list-style-type: square;
  }

  & sup {
    font-size: 12px;
    vertical-align: baseline;
    top: -0.6em;
    position: relative;

    & a {
      border: 0;
    }
  }
}

blockquote {
  font-size: 1.5em;
  color: var(--colorTextSecondary);
  margin-top: 1.5em;
  margin-bottom: 1.5em;
  text-align: center;
  line-height: 1.25;

  @media (min-width: 63.125rem) {
    margin-left: -2em;
    margin-right: -2em;
  }

  & p {
    margin-bottom: 0.5em;
    font-style: italic;
  }

  & footer {
    font-size: 0.75em;
  }

  & cite {
    font-style: normal;
    font-weight: 700;
  }
}

.note {
  font-size: 1em;
  border-left: 2px solid var(--colorHighlight);
  color: var(--colorTextSecondary);
  padding-left: 25px;
  margin: 1.5em 0;
}

figure {
  margin: 1.5em 0;
}

figcaption {
  color: var(--colorTextSecondary);
  font-size: 0.85em;
  text-align: center;
}

b, strong {
  font-weight: var(--bold);
}

pre, code {
  font-size: 14px;
  text-shadow: none !important;
}

p > code {
  border: 1px solid var(--colorSecondary);
  border-radius: 4px;
  font-size: 0.9em;
  vertical-align: top;
  padding: 2px 5px;
  line-height: 1.8;
}

::selection {
  background-color: var(--colorHighlight);
  color: #fff;
}

.about__summary {
  font-style: italic;
  color: var(--colorTextSecondary);
  font-size: 1.1em;
  line-height: 1.35;

  @media (min-width: 36.25rem) {
    width: 75%;
  }
}

code[class*="language-"], pre[class*="language-"], code, pre {
  font-family: menlo, monaco, monospace, serif !important;
}
